.main-nav {
  display: flex;
  justify-content: space-around;
  padding: 8px 20px;
  border-bottom: 1px solid; 
}

.main-nav-item {
  -webkit-transition: box-shadow 0.1s ease-in-out;
  transition: box-shadow 0.1s ease-in-out;
  border-radius: 4px;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-nav-item.selected {
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  background-color: #7fc7ff;
}
.moves-list {
  background-color: #f1f1f1;
  padding: 20px;
  border-radius: 5px;
}

.moves-list-title {
  font-weight: 600;
  padding: 0 0 10px 0;
}

.moves-list-item:first-child {
  border-top: 1px solid #cfcfcf;
}
.moves-list-item {
  border-bottom: 1px solid #cfcfcf;
  padding: 20px 0;
}
.moves-list-item .item-date{
  margin: 10px 0 2px 0;
}


.user-details {
  padding: 20px;
  margin: 0 0 20px 0;
}

.user-name {
  margin: 0 0 20px 0;
  font-size: 22px;
}

.user-coins-count {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-rate {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center;
}

.user-coins-count img, .user-coins-rate img {
  margin: 0 5px 0 0;
}


.signup {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 50px 0 0 0;
}

.signup-icon {
    margin: 0 0 20px 0;
}

.signup-form {
    display: flex;
    align-items: center;
    flex-direction: column;
}
.signup-title {
    margin: 0 0 10px 0;
}

.actions-container {
    margin: 10px 0;
}
.chart {
  margin: 20px 0;
}

.chart-name {
  margin: 5px 0;
}

.chart-desc {
  font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
  margin: 0 0 20px 0;
  border-bottom: 1px solid #f4f5f7;
}
.contact-preview {
  display: inline-block;
  padding: 0 25px;
  height: 50px;
  width: 240px;
  font-size: 17px;
  line-height: 50px;
  border-radius: 25px;
  background-color: #f1f1f1; 
}
  
.contact-preview img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%; 
}

.contacts-list-item {
  margin: 10px 0; 
}

.contact-filter input {
  width: 100%;
  height: 30px;
  padding: 0 10px;
  font-size: 12px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #D8D8D8; 
}
  
.contact-filter input:focus {
  outline: none; 
}

.contacts-page {
  position: relative;
  height: 100%; 
}

.contacts-page .search-container {
  height: 40px;
  padding: 0 10px; 
}

.contacts-page .contacts-container {
  max-height: 360px;
  overflow-x: auto; 
}

.contacts-page .action-container {
  position: absolute;
  bottom: -3px;
  right: 10px;
}

.transfer-coins {
  position: relative;
  padding: 20px;
  background: #7fc7ff;
  border-radius: 5px;
}

.transfer-form {
  display: flex;
  justify-content: start;
  margin: 20px 0 0 0;
}

.input-amount {
  width: 100%;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #D8D8D8;
  background-color: #7fc7ff;
  padding: 0 0 0 5px;
  margin: 0 20px 0 5px;
}

.message-container {
  position: absolute;
  bottom: 2px;
  font-size: 13px;
  color: #008c07;
}

.error {
  color: red;
}
.contact-details-header {
  display: flex;
  justify-content: space-between;
  height: 40px; 
}
.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0; 
}

.contact-details .contact-details-row {
  margin: 10px 0 0 0; 
}

.moves-list-container, .transter-coins-container {
  margin: 20px 0;
}
.input-container {
    align-items: center;
    display: flex;
    justify-content: start;
    margin: 20px 0 0 0; 
}

.input-container label {
    width: 70px;
    text-transform: capitalize;
    margin: 0 15px 0 0;
}

.input-container input.input {
    width: 100%;
    height: 30px;
    padding: 0 10px;
    font-size: 12px;
    box-sizing : border-box;
    border-radius: 5px;
    border: 1px solid #D8D8D8;
}


.input:focus {
    outline: none;
}

.input::-webkit-input-placeholder { /* WebKit browsers */
    text-transform: capitalize;
}
.input:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    text-transform: capitalize;
}
.input::-moz-placeholder { /* Mozilla Firefox 19+ */
    text-transform: capitalize;
}
.input:-ms-input-placeholder { /* Internet Explorer 10+ */
    text-transform: capitalize;
}
.contact-edit-header {
  display: flex;
  justify-content: space-between;
  height: 40px; 
}

.contact-edit-header a:not(:last-child) {
  margin: 0 10px 0 0; 
}

.contact-edit .form-actions-container {
  display: flex;
  justify-content: flex-end;
  margin: 20px 0 0 0; 
}

.contact-edit .form-actions-container button {
  cursor: pointer; 
}

.app {
  max-width: 300px;
  padding: 20px;
  border: 1px solid black;
  border-radius: 5px;
  margin: 30px auto; 
}

.app .app-content {
  padding: 20px 0 0 0;
  height: 400px;
  overflow-x: hidden;
  overflow-y: auto; 
}

.app [class^="flaticon-"]:before,
.app [class*=" flaticon-"]:before,
.app [class^="flaticon-"]:after,
.app [class*=" flaticon-"]:after {
    margin: 0; 
}

body {
    margin: 0;
    padding: 0;
}

a, a:visited, a:hover, a:active {
    text-decoration: none;
    color: inherit; 
}

ul {
    margin: 0;
    padding: 0; 
}
  
li {
    list-style: none; 
}
